import ReservationForm from "../components/ReservationForm";
import ResponseMessage from "../components/ResponseMessage";
import Accordion from "../components/Accordion";
import PageTitle from "../components/PageTitle";
import Layout from "../components/layout";
import React from "react";

class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      responseData: null,
      isMobile: true,
      controler: { identifier: "", group: "" }
    };
    this.resizeHandler = this.resizeHandler.bind(this);
    this.breakpoint = 600;
    this.bgImage = [
      "http://news.hiltonworldwide.com/assets/HWW/images/newsroom/2011/HiltonGuangzhouTianheExterior_Night_FP.jpg",
      "https://res.cloudinary.com/lastminute/image/upload/c_scale,w_630/q_auto/v1429779489/NYCBP_Exterior_2011_HR_ukzskh.jpg",
      "https://www.lastminute.com/hotels/img/city/New-York-NY-US.jpg?2.42.0-063942240519"
    ];
  }

  componentDidMount() {

    window.addEventListener("resize", this.resizeHandler);
    // this.setState({isMobile:})
    this.resizeHandler()
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resizeHandler);
  }

  groupFn = o => {
    this.setState({ controler: { identifier: o.myID, group: o.groupID } });
  };

  callbackFn = bool => {
    // Do some side effect
    console.log("callbackFn(" + bool + ")");
  };

  resizeHandler() {
    // const debounce = (func, delay) => {
    //   let inDebounce;
    //   return (function() {
    //     const context = this;
    //     const args = arguments;
    //     clearTimeout(inDebounce);
    //     inDebounce = setTimeout(() => func.apply(context, args), delay);
    //   })();
    // };
    const resizeLayout = () => {
      if (this.state.isMobile !== window.innerWidth < this.breakpoint) {
        this.setState({
          isMobile: window.innerWidth < this.breakpoint
        });
      }
    };
    // debounce(resizeLayout, 200);
    resizeLayout();
  }

  responseFn = message => {
    this.setState({
      responseData: message
    });
  };

  render() {
    return (
      <Layout isMobile={this.state.isMobile} backgroundImage={this.bgImage}>
        <PageTitle isMobile={this.state.isMobile} ><div className="accordionTitle" id="accordionClicker1">Make your reservations</div></PageTitle>

        <Accordion
          ID="me_1"
          actionID="accordionClicker1"
          groupID="AAA"
          groupFn={this.groupFn}
          controler={this.state.controler}
          callbackFn={this.callbackFn}
          initOpen={true}
        >
          <ReservationForm callbackFn={this.responseFn} />
        </Accordion>

        <PageTitle isMobile={this.state.isMobile} ><div className="accordionTitle" id="accordionClicker2">Results</div></PageTitle>
        <Accordion
          ID="me_2"
          actionID="accordionClicker2"
          groupID="BBB"
          groupFn={this.groupFn}
          controler={this.state.controler}
          callbackFn={this.callbackFn}
          initOpen={false}
        >
          <ResponseMessage message={this.state.responseData} />
        </Accordion>
      </Layout>
    );
  }
}

export default Index;
